@mixin flex-row-center {
  display: flex;
  justify-content: center;
  align-items: center;
}

@mixin flex-column-center {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

@mixin flex-row-reverse-center {
  display: flex;
  flex-direction: row-reverse;
  justify-content: center;
  align-items: center;
}